<template>
  <div
    id="app"
    @mouseover="OperatingWebsite()"
  >
    <router-view></router-view>
  </div>
</template>
<script>
export default {
    data() {
    return {
      currentTime: new Date().getTime() 
    };
  },
  methods: {
    OperatingWebsite() {
      let currentTime = this.currentTime;
    //   console.log(currentTime, "currentTime");
      let lastTime = new Date().getTime();
    //   console.log(lastTime, "lastTime");
      let timeOut = 15 * 60 * 1000; //设置时间 10分钟
      if (lastTime - currentTime > timeOut) {
        // 未操作页面，跳转登录页面
        this.currentTime = new Date().getTime(); 
        sessionStorage.clear();
        this.$router.push({
          path: "/login"
        });
        this.$message.error("15分钟未操作，请重新登录！")
      } else {
        this.currentTime = new Date().getTime(); 
      }
 
      // const truthPathQuery = this.$route.query.type;
      // const truthPath = this.$Base64.decode(truthPathQuery); //点击登录的时候跳转这个地址
      
    },
  }
}
</script>

<style>
@import "../static/css/main.css";
@import "../static/css/color-dark.css"; /*深色主题*/
/*@import "../static/css/theme-green/color-green.css";   浅绿色主题*/
</style>



